* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; /* to avoid padding summing height (content-box) */
}

html {
  font-size: 62.5%;
}

.App {
  text-align: center;
}

.App-header {
  float: left;
  width: 40%;
  height: 700px;
}

.App-content {
  float: right;
  width: 60%;
  height: 1000px;
}

.App-footer {
  float: left;
  width: 40%;
  height: 300px;
}

@media screen and (min-width: 780px) and (max-width: 1024px) {
}

@media screen and (min-width: 480px) and (max-width: 780px) {
}

@media screen and (max-width: 480px) {
  .App-header {
    float: left;
    width: 100%;
  }

  .App-content {
    float: left;
    width: 100%;
  }

  .App-footer {
    float: left;
    width: 100%;
  }
}
