.header {
  position: relative;
  overflow: hidden;
}

.header .background_image {
  z-index: -1;
  position: absolute;
  left: 0px;
  bottom: -300px;
  object-fit: cover;
  width: 100%;
  height: 1000px;
  opacity: 0.9;
}

.image {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateY(-50%);
  transform: translateX(-50%);
  height: 15rem;
  border-radius: 100%;
  display: block;
}

.presentation_text {
  position: relative;
  top: 35%;
  color: rgb(255, 255, 255);
  text-shadow: 0 0 1rem rgb(0, 0, 0);
  font-size: 2.5rem;
}

.presentation_text strong {
  font-size: 3.5rem;
}
